@import url(https://fonts.googleapis.com/css?family=Poppins);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px; }
  ::-webkit-scrollbar-track {
    background: white; }
  ::-webkit-scrollbar-thumb {
    background: #61892f; }
    ::-webkit-scrollbar-thumb:hover {
      background: #86c232; }

#htmlBody {
  height: 100%;
  margin: 0;
  font-family: "Poppins", sans-serif;
  background-color: #222629;
  color: black; }

.home-feed {
  background-color: #F5F6F7;
  padding: 10px;
  border-radius: 3px;
  border: 1px solid lightgray; }

.signin {
  width: 500px;
  margin: 100px auto;
  background-color: #86c232;
  padding: 30px;
  color: #222629; }

.container {
  min-height: calc(100vh - 350px); }

.app nav {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1; }

.navbar {
  background-color: #86c232;
  margin-bottom: 30px;
  border-bottom: 1px solid navy; }
  .navbar-logo {
    font-size: 12px;
    margin: 0px;
    color: black; }
  .navbar-title {
    text-align: left; }
  .navbar-list {
    margin: 0; }
    .navbar-list li {
      font-size: 25px;
      margin: 0 30px 0 0;
      list-style: none;
      display: inline-block; }
      .navbar-list li * {
        color: black; }
        .navbar-list li *:hover {
          color: #F5F6F7;
          text-decoration: none;
          background-color: #61892f;
          padding: 10px;
          margin: 0 -10px;
          border-radius: 5px; }

.home-activity {
  top: -240px;
  bottom: 300px; }
  .home-activity-list {
    background-color: #F5F6F7;
    margin-bottom: 20px; }
  .home-activity-scrolling {
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap; }

.home-trends {
  margin-bottom: 30px; }

.show-list {
  display: inline-block; }
  .show-list-item {
    display: inline-block;
    width: 120px;
    vertical-align: top;
    height: 210px;
    margin: 20px 10px; }
    .show-list-item img {
      box-shadow: 2px 2px 5px black; }

.show-component {
  text-align: center;
  font-size: 16px;
  position: relative; }
  .show-component-poster {
    width: 80%;
    min-width: 70px;
    cursor: pointer; }
  .show-component-title {
    white-space: normal;
    margin-top: 8px; }
  .show-component-close {
    position: absolute;
    top: -10px;
    right: 1px;
    color: lightgray;
    border-radius: 50px;
    height: 22px;
    width: 22px;
    cursor: pointer; }
  .show-component-close:hover {
    background-color: gray; }

.show-search {
  margin: 0 0 15px 0; }

.modal {
  white-space: normal;
  text-align: center; }
  .modal .modal-content {
    background-color: #F5F6F7;
    width: 80vw; }
    .modal .modal-content .modal-body {
      padding: 0;
      overflow: auto; }
      .modal .modal-content .modal-body .modal-image {
        width: 100%; }
      .modal .modal-content .modal-body .modal-description {
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 5px; }
      .modal .modal-content .modal-body .modal-description {
        color: white;
        position: absolute;
        bottom: 0;
        left: 0;
        margin: 10px;
        padding: 10px;
        text-shadow: 1px 1px 1px #222629;
        letter-spacing: .1em; }
      .modal .modal-content .modal-body .modal-details {
        color: #86c232; }
    .modal .modal-content .modal-footer button.dropdown-item {
      cursor: pointer; }

.shows-genre {
  margin-top: 40px; }
  .shows-genre-list {
    cursor: pointer; }

.shows-scroll {
  text-align: center;
  margin: 10px 0 -40px 0; }
  .shows-scroll-number {
    margin: 0 20px; }
  .shows-scroll img {
    cursor: pointer;
    display: inline-block;
    width: 30px; }

.footer {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  width: 100%;
  text-align: center;
  background-color: #86c232;
  color: black;
  margin-top: 120px;
  border-top: 1px solid black;
  padding: 5px; }

